import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);
    this.state={
      insertIndex: 0,
      insertText: '',
      insertItems:[],
    };
  }

  // 入力値の取得処理
  onInputChangeHandler = (text) => {
    this.setState({
      insertText: text.target.value
    })
  }

  // 追加ボタン押下処理
  onClickHandler= () => {
    this.setState({
      insertItems: this.state.insertItems.concat(this.state.insertText)
    })
    console.log(this.state.insertItems)
  }

  // 削除ボタン押下処理
  onClickDelete(idx) {
    this.state.insertItems.splice(idx, 1)
    this.setState({
      insertItems:this.state.insertItems
    })
  }

  render() 
    {return (
      <div className="App">
        <header className="App-header">
          <input type='text' className='inputText' onChange={this.onInputChangeHandler} />
          <button id='buttonAction' onClick={this.onClickHandler}>追加</button>
          {this.state.insertItems.map((itm , idx) => (
            <li key={idx}>{itm} <button onClick={this.onClickDelete.bind(this, idx) }>削除</button></li>
          ))}
        </header>
      </div>
    );
  }
}

export default App;
